import Vue from 'vue'
import Router from 'vue-router'

import calendar from '@/components/calendar/calendar'
import editEvent from '@/components/calendar/editEvent'
import loginPage from '@/components/calendar/loginPage'
import mainPage from '@/components/calendar/mainPage'
import bookIt from '@/components/calendar/bookIt'
import employee from '@/components/calendar/employee'
import addNewUser from '@/components/calendar/addNewUser'
import editUser from '@/components/calendar/editUser'
import infoEvent from '@/components/calendar/infoEvent'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'loginPage',
      component: loginPage
    },
    {
      path: '/bookIt/:roomId',
      name: 'bookIt',
      component: bookIt
    },
    {
      path: '/mainpage',
      name: 'mainPage',
      component: mainPage,
      children: [
        {
          path: 'calendar/:id',
          components:{calendar: calendar}
        }
      ]
    },
    {
      path: '/calendar/:id',
      name: 'calendar',
      component: calendar,
    },
    {
      path: '/employee/:roomId',
      name: 'employee',
      component: employee,
    },
    {
      path: '/addNewUser',
      name: 'addNewUser',
      component: addNewUser,
    },
    {
      path: '/editUser/:userId',
      name: 'editUser',
      component: editUser,
    },
    {
      path: '/editEvent/:bedroomId/:userId/:eventId/:startTime/:eventDate',
      name: 'editEvent',
      component: editEvent
    },
    {
      path: '/infoEvent/:bedroomId/:userId/:eventId/:startTime/:eventDate',
      name: 'infoEvent',
      component: infoEvent
    }
  ]
})



